import React from 'react';
import { Link } from 'react-router-dom';
import Card from '@material-ui/core/Card';
import { CardContent, CardMedia } from '@material-ui/core';

export const HeroCard = (state) => {
  const { id, superhero, alter_ego, first_appearance, characters, image } = state;

  return (
    <Card
      className="card ms-3 animate__animated animate__fadeIn"
      style={{ maxWidth: 540, background: 'blueBright' }}
    >
      <CardContent className="row no-gutters">
        <CardMedia className="col-md-4">
          <img src={image} className="card-img" alt={superhero} />
        </CardMedia>
        <div className="col-md-8">
          <div className="card-body">
            <h5 className="card-title"> {superhero} </h5>
            <p className="card-text"> {alter_ego} </p>

            {alter_ego !== characters && <p className="card-text"> {characters} </p>}

            <p className="card-text">
              <small className="text-muted"> {first_appearance} </small>
            </p>

            <Link to={{ pathname: `/hero/${id}`, state }}>Más...</Link>
          </div>
        </div>
      </CardContent>
    </Card>
  );
};
